import * as React from "react"
import { Link } from "gatsby"
import { Col, Row, Image } from "react-bootstrap"

import Layout from "../components/layout"
import Seo from "../components/seo"
import Hero from "../components/hero"

const IndexPage = ({ location }) => {
  const [exclamationPoint, setExclamationPoint] = React.useState("\xa0")
  const [startText, setStartText] = React.useState("\xa0")
  const [display, setDisplay] = React.useState(false)

  React.useEffect(() => {
    async function typeSentence(sentence, delay = 100) {
      const letters = sentence.split("")
      for (let i = 0; i < letters.length; i++) {
        await waitForMs(delay)
        setStartText(prevState => prevState + letters[i])
      }
      return
    }

    function waitForMs(ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    }

    const exclamationPointTimer = setTimeout(() => {
      setExclamationPoint("Warning!")
    }, 2000)
    const startTextTimer = setTimeout(() => {
      setStartText("")
      typeSentence("Your Friday night is now mine! >:D")
    }, 3000)
    const dateTextTimer = setTimeout(() => {
      setDisplay(true)
    }, 7000)
    return () => {
      clearTimeout(exclamationPointTimer)
      clearTimeout(startTextTimer)
      clearTimeout(dateTextTimer);
    }
  }, [])

  return (
    <Layout location={location}>
      <Seo title={"Birthday Boy"} />
      <Row className="">
        <Col className="my-auto">
          <h1
            className="glow position-relative-zindex"
            style={{ fontSize: "25px", margin: "0, -20px" }}
          >
            Alexander's 25th Birthday
          </h1>
          <Image fluid src={"/birthday/assets/IMG_20191117_155740 (1).png"} alt="Logo" />
        </Col>
        <a href="" className="mb-4">
          <span style={{ fontSize: 20, color: "var(--bs-pink)" }}>
            {exclamationPoint}
          </span>
          <br></br>
          {startText}
          <br></br>
          <br></br>
          <span className="fade" style={{opacity: display ? "1" : "0" }}>
          July 14th, 2023
          </span>
        </a>
        <Row className="fade" style={{ paddingRight: 0, opacity: display ? "1" : "0" }}>
          <Col className="p-0" xs={4}>
            <p>&nbsp;6:30pm</p>
          </Col>
          <Col style={{ paddingRight: 0 }}>
            <p className="text-start">
              Dinner 🍣
              <br></br>
              <span className="text-muted">Sushi Junai 2</span>
            </p>
          </Col>
        </Row>
        <Row className="fade" style={{ paddingRight: 0, opacity: display ? "1" : "0" }}>
          <Col className="p-0" xs={4}>
            <p>&nbsp;8:30pm</p>
          </Col>
          <Col style={{ paddingRight: 0 }}>
            <p className="text-start">
              Kickback 🎉
              <br></br>
              <span className="text-muted">
                1725 Gaylord Drive, Austin, Texas
              </span>
            </p>
          </Col>
        </Row>
        <Row className="fade" style={{ paddingRight: 0, opacity: display ? "1" : "0" }}>
          <p className="text-start">There'll be:</p>
          <Col className="p-0" xs={6}>
            <p>Drinks</p>
          </Col>
          <Col style={{ paddingRight: 0 }} xs={6}>
            <p>Karaoke</p>
          </Col>
          <Col xs={6}>
            <p>Snacks</p>
          </Col>
          <Col style={{ paddingRight: 0 }} xs={6}>
            <p>You!</p>
          </Col>
        </Row>
      </Row>
    </Layout>
  )
}

export default IndexPage
